/**
 * 这是文章详情页面
 * 
*/

import React from 'react'
import styles from './albumdetail.module.less'
import { PageHeader, Divider, Image } from 'antd'
import { _getPhotoList } from '../../api/album'

const errImage = "data:image/png;base64,"
const height = 180
export default class AlbumDetail extends React.Component {


    constructor (props) {
        super(props)

        this.state ={
            loading: false,
            list: [],
            listQuery: {
                id: undefined,
                page: 1,
                limit: 20
            },
        }
    }

    componentDidMount () {
        const { id } = this.props.match.params
        const listQuery = { ...this.state.listQuery, id}
        this.setState({listQuery}, () => {
            this.getPhotoList()
        })
       
    }

    getPhotoList = async () => {
        this.setState({ loading: true })
        let result = await _getPhotoList(this.state.listQuery)
        console.log(result)
        this.setState({ loading: false, list: result.data })
    }


    render () {
        const { list } = this.state
        return (
            <div className={styles['page']}>
                <div className={styles['albumdetail']}>
                    <PageHeader
                            onBack={() => {window.history.go(-1)}}
                            title="返回"
                        />
                    <Divider dashed />

                    <div className={styles['albumdetail-container']}>
                        <Image.PreviewGroup>
                            {
                                list.map((ITEM, INDEX) => {
                                    return (
                                            <Image
                                                key={INDEX}
                                                height={height}
                                                alt={INDEX+1}
                                                preview={{
                                                    visible: true,
                                                    mask: <div>{INDEX + 1}</div>,
                                                    current: INDEX
                                                }}
                                                style={{paddingLeft: 5, paddingLeft: 5}}
                                                width={300}
                                                src={ITEM.url}
                                                fallback={errImage}
                                            />
                                       
                                    )
                                })
                            }
                           
                        </Image.PreviewGroup>
                    </div>
                </div>
                
            </div>
        )
    }
}